@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;1,600&display=swap);
* {
  box-sizing: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body{background-color:black}svg{position:absolute;height:100vh;width:100vw;overflow:visible}.grid-container{display:flex;width:100vw;height:100vh;background-color:black}.swiss-circle-container{position:relative;margin-left:-2em;margin-top:-1em}.swiss-circle-container #circ-1{position:relative;stroke-width:2px;stroke:#d4f0fc}.swiss-circle-container #circ-2{position:relative;stroke-width:4px;stroke:#89d6fb}.swiss-circle-container #circ-3{position:relative;stroke-width:8px;stroke:#02a9f7}.swiss-circle-container #circ-4{position:relative;stroke-width:16px;stroke:#02577a}.swiss-circle-container #circ-5{position:relative;stroke-width:32px;stroke:#01303f}#title-container{position:absolute;width:auto;left:50%;top:50%;transform:translate(-56%, -50%);margin:0}#name{color:white;font-size:3.8em;font-weight:600}#sub-title{color:white;font-weight:400;font-size:2em;position:relative}@media screen and (max-width: 768px){#title-container{transform:translate(-56%, -50%)}#title-container #name{width:auto;font-size:3.42em}}@media screen and (max-width: 500px){#title-container{transform:translate(-56%, -50%)}#title-container #name{width:auto;font-size:3em}}

.about-me-container{position:relative;width:100vw;height:100vh;background-color:#54595f}.about-me-content{position:relative;background-color:black;left:50%;top:50%;transform:translate(-50%, -50%);display:grid;grid-template-columns:repeat(auto-fit, minmax(240px, 1fr));align-items:center}.about-me-content .about-me-text-container{margin-top:2rem;text-align:left;max-width:600px}.about-me-content .about-me-text-container>h2{color:white;font-size:3.8rem;margin-bottom:2.5rem;padding:1.5rem}.about-me-content .about-me-text-container>p{color:white;font-size:1.2rem;margin-bottom:2rem;line-height:1.7rem;padding:1.5rem}.about-me-graphic-container{position:relative;min-height:300px;top:50px;margin:0 auto}.about-me-graphic-container #laptop-graphic{position:absolute;left:50%;top:50%;opacity:0;transform:translate(-50%, 25%) scale(0.9);z-index:10}.about-me-graphic-container #laptop-graphic>img{position:relative;left:0;top:0}.about-me-graphic-container #sass-graphic{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);opacity:0}.about-me-graphic-container #node-graphic{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);opacity:0}.about-me-graphic-container #react-graphic{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);opacity:0}.about-me-graphic-container #firebase-graphic{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);opacity:0}.about-me-graphic-container #gsap-graphic{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);opacity:0}@media (max-width: 1000px){.about-me-graphic-container{transform:scale(0.6)}}

.nav-container{position:fixed;width:100vw;height:6em;display:grid;grid-template-columns:150px 1fr 1fr 2fr 150px;grid-template-rows:4em;top:2em;background-color:#2d2f31c2;z-index:2}.nav-container .logo-container{position:relative;width:6em;height:auto;grid-column:2/3}.nav-container .logo-container #nav-logo{position:absolute;left:0;top:3px}.nav-container .link-container{position:relative;grid-column:4/5;min-width:500px;display:flex;align-items:center;justify-content:space-around;font-size:2em;font-weight:500;overflow:visible;margin-top:1em}.nav-container .link-container :nth-child(n){cursor:pointer}.nav-container .link-container .home-link{color:#429ffe}.nav-container .link-container .home-link:after{content:"";position:absolute;top:inherit;left:inherit;width:inherit;height:2px;background-color:#429ffe}.nav-container .link-container .about-me-link{color:#e96feb}.nav-container .link-container .skills-link{color:#a761ff}.nav-container .link-container .projects-link{color:#ff6057}.nav-button{position:fixed;display:none;width:5em;height:5em;background-color:#2d2f31b7;border:1px solid white;left:calc(100% - 2em);top:calc(100% - 2em);border-radius:50%;transform:translate(-100%, -100%);z-index:999}.nav-button .hamburger-nav{position:absolute;width:3em;height:0.3em;background-color:white;left:50%;top:50%;transform:translate(-50%, -50%);cursor:pointer}.nav-button .hamburger-nav:after{content:"";position:absolute;width:inherit;height:inherit;background-color:white;left:inherit;transform:inherit;top:1em}.nav-button .hamburger-nav:before{content:"";position:absolute;width:inherit;height:inherit;background-color:white;left:inherit;transform:inherit;top:-0.7em}.mobile-nav-container{position:fixed;width:100vw;height:100vh;top:-150%;background-color:#2d2f31;z-index:998}.mobile-nav-container .mobile-link-container{position:relative;display:flex;flex-direction:column;width:calc(100vw - 1.5em);height:calc(100vh - 15em);left:50%;top:50%;transform:translate(-50%, -50%)}.mobile-nav-container .mobile-link-container>a{position:relative;width:100%;height:100%;margin-bottom:0.5em;text-align:center;font-size:1.7em;font-weight:700;color:#929191}.mobile-nav-container .mobile-link-container>a>span{position:absolute;top:50%;transform:translate(-50%, -50%)}.mobile-nav-container .mobile-link-container #mobile-home-link{color:#429ffe}.mobile-nav-container .mobile-link-container #mobile-about-me-link{color:#e96feb}.mobile-nav-container .mobile-link-container #mobile-skills-link{color:#a761ff}.mobile-nav-container .mobile-link-container #mobile-projects-link{color:#ff6057}@media (max-width: 768px){.nav-container{position:fixed;width:100vw;height:6em;display:none;grid-template-columns:150px 1fr 1fr 2fr 150px;grid-template-rows:4em;top:calc(100% - 7em);background-color:#2d2f31;z-index:2}.nav-button{display:inline-block}}@media (max-width: 768px){.nav-container{position:fixed;width:100vw;height:6em;display:none;grid-template-columns:150px 1fr 1fr 2fr 150px;grid-template-rows:4em;top:calc(100% - 7em);background-color:#2d2f31;z-index:2}.nav-button{display:inline-block}}@media (max-width: 480px){.nav-button{width:3.5em;height:3.5em}.nav-button .hamburger-nav{width:2em;height:0.2em}.nav-button .hamburger-nav:after{top:0.8em}.nav-button .hamburger-nav:before{top:-0.6em}}

.skills-title{position:relative;display:flex;width:auto;height:45px;margin-top:2em}.skills-title #tools-icon{position:relative;margin:0.2rem;width:20px;height:100%;align-self:center;fill:white}.skills-title>h3{margin-left:0.5em;margin-top:2px;align-self:center;color:white;font-size:1.7em}.skills-container{position:relative;display:block;width:calc(100vw - 4em);height:100vh;left:50%;transform:translateX(-50%)}.skills-container figure{width:50px}.skills-container figure>img{position:relative;left:50%;transform:translateX(-50%)}.skills-container>ul{display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));grid-gap:1rem;margin:0;padding:0}.skills-container>ul>li{display:flex;margin-top:1em;max-width:364px;min-width:300px;justify-content:flex-start;border:1px solid #cac8c8;border-radius:0.5rem;padding-top:15px;padding-bottom:10px}.skills-container>ul>li div>h6:first-child{color:white;font-size:0.775rem}.skills-container>ul>li div>h6:last-child{color:white;font-size:0.7rem;margin-top:0.4rem;font-weight:normal}.skills-container>ul>li>*:nth-child(n){margin-left:1.2rem}li{list-style:none}

